/* Breakpoint mixins */
@mixin for-narrow-layout {
  @media (max-width: $break-narrow) {
    @content;
  }
}

@mixin for-normal-layout {
  @media (min-width: $break-narrow) {
    @content;
  }
}

/* Color Theme */

@mixin color-theme-basic($theme) {
  color: theme-color($theme, main-text-clr);
  background-color: theme-color($theme, main-bg-clr);
}


/* Reset */

@mixin reset-textarea {
  border: 2px solid transparent;
  overflow: auto;
  outline: none;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
  resize: none;
  font-size: inherit;
  font-family: inherit;
}